<template>
  <div class="wrapper-gutter">
      <h2 class="upcase">sandbox</h2>

      <!-- overlap image row -->
      <div class="row gutter-vertical-padding">
        
        <div class="span-8-lg relative ind-2">
          <div class="overflow-image ratio-3-2 border-blue">
            <image-component v-bind:image="example_image"></image-component>
          </div>
        </div>

        <div class="span-8-lg relative ind-1">

          <div class="slide-outer ratio-square ind-1">
            <div class="slide-content">
              <h2 class="upcase">why should I care?</h2>
              <p>
                Too often, childhood trauma is misdiagnosed and misunderstood. On the outside, its victims look just like regular kids. But on the inside, these kids have been affected emotionally, mentally, and even physically. So much so that their brain development can actually be affected and changed. 
              </p>
              <p>
                As with an iceberg,  theres much more below the surface than above it. Issues that present themselves in a classroom such as absenteeism, poor grades or aggression often are symptoms of the very broad-based, complex issue of trauma.
              </p>
              <p>
                The good news is, we can help kids heal. 
              </p>
            </div>
          </div>

          <crosshair-background></crosshair-background>
          <span-background></span-background>

        </div>
      </div>

    
      <!-- full span row -->
      <!-- todo: add margin auto inner content with loads of padding -->
      <!-- todo: fix all flexbox non-set height components for IE -->

      <div class="row gutter-vertical-padding">
        <div class="span-16-lg relative">

          <div class="relative ind-1 ratio-20-9 full-span-outer">
            <div class="full-span-inner">
              <h1 class="upcase">HELPING children IMPACTED BY trauma TO HEAL AND THRIVE</h1>
              <p>Exposure to violence in the community or at home can impact students  psychologically, socially, academically, and behaviorally. It often results in trauma, diminished health outcomes, low academic achievement, classroom management issues and a hastened dispatch to child welfare or juvenile justice system--particularly for students of color.</p>
              <p>While the issue of childrens exposure to violence and trauma is not new for schools, there are now even more promising practices that can help our schools do what they do best - educate.</p>
            </div>
          </div>
          
          <crosshair-background></crosshair-background>
          <span-background></span-background>

        </div>
      </div>

      <!-- two rollover cards slide -->
      
      <div class="row">
        <div class="span-15-lg offset-1-lg span-15-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs">
          <p class="large">Did you know?</p>
        </div>
      </div>

      <div class="row gutter-vertical-padding">
        <rollover-card spanclass="span-6-lg offset-1-lg span-6-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs bump-up" ratioclass="ratio-5-4-card">
          <h3>col one</h3>
        </rollover-card>
        <rollover-card spanclass="span-6-lg offset-2-lg span-6-md offset-2-md span-14-sm offset-1-sm span-14-xs offset-1-xs bump-down" ratioclass="ratio-5-4-card">
          <h3>col two</h3>
        </rollover-card>
      </div>
      
      <!-- flip card slide -->

      <div class="row">
        <div class="span-15-lg offset-1-lg span-15-md offset-1-md span-16-sm span-14-sm offset-1-sm span-14-xs offset-1-xs">
          <p class="large">There are different types of trauma that kids might experience and where they might experience it.</p>
        </div>
      </div>

      <div class="row gutter-vertical-padding">
        <flip-card icon="culture"></flip-card>
        <flip-card icon="role-models"></flip-card>
        <flip-card icon="networks"></flip-card>
      </div>


      <!-- progress selector slide -->

      <div class="row">
        <div class="span-15-lg offset-1-lg span-15-md offset-1-md">
          <p class="large">Protective Factors that Promote Resiliency: Individual</p>
        </div>
      </div>

      <div class="row gutter-vertical-padding align-center">

        <div class="span-9-lg offset-1-lg span-8-md overflow-hidden">
          <progress-selector>one</progress-selector>
          <progress-selector>two</progress-selector>
          <progress-selector>three</progress-selector>
          <progress-selector>four</progress-selector>
          <progress-selector>five</progress-selector>
          <progress-selector>six</progress-selector>
          <progress-selector>seven</progress-selector>
        </div>

        <rollover-card spanclass="span-4-lg offset-1-lg span-7-md offset-1-md span-16-sm span-16-xs" ratioclass="ratio-3-4">
          <h3>content here</h3>
        </rollover-card>

      </div>

      
      <!-- fixed navbar, will be moved up to parent scope later -->
      <navbar></navbar>

  </div>
</template>

<script>

  import crosshair_background from '../components/atoms/crosshair_background'
  import span_background from '../components/atoms/span_background'
  import arrow from '../components/atoms/arrow'
  import image_component from '../components/atoms/image_component'

  import progress_selector from '../components/molecules/progress_selector'
  import popup_notification from '../components/molecules/popup_notification'
  import flip_card from '../components/molecules/flip_card'
  import rollover_card from '../components/molecules/rollover_card'

  import navbar from '../components/organisms/navbar'
  
  import correct from '../components/svg/correct'
  import incorrect from '../components/svg/incorrect'
  import collaborate from '../components/svg/collaborate'

  export default {
    data () {
      return {
        example_image: 'https://s3-us-west-2.amazonaws.com/fwov-vpv/temp.png'
      }
    },
    components: {
      'span-background': span_background,
      'crosshair-background': crosshair_background,
      'arrow': arrow,
      'image-component': image_component,

      'progress-selector': progress_selector,
      'popup-notification': popup_notification,
      'flip-card': flip_card,
      'rollover-card': rollover_card,

      'navbar': navbar,

      'correct': correct,
      'incorrect': incorrect,
      'collaborate': collaborate
    }
  }
</script>

<style scoped lang='scss'>
  @import '../styles/variables';

  .bump-up {
    transform: translateY(-18px);
  }

  .bump-down {
    transform: translateY(18px);
  }

  .bump-up, .bump-down {
    @media screen and (max-width: $sm-max) {
      transform: none;
      margin-bottom: 36px;
    }
  }

  .screen-height {
    height: 100vh;
  }

  .wrapper-gutter {
    position: relative;
    margin: auto;
    max-width: calc(100% - 80px - 80px);
    @media screen and (max-width: $sm-max) {
      max-width: 100%;
    }
  }

  .gutter-vertical-padding {
    padding-top: 80px;
    padding-bottom: calc(96px + 80px);
    @media screen and (max-width: $sm-max) {
      padding-top: 0px;
      padding-bottom: 60px;
    }
  }


  // vertical align without flexbox
  // see http://zerosixthree.se/vertical-align-anything-with-just-3-lines-of-css/

  .overflow-image {
    position: relative;
    width: 100%;
    padding-right: calc(100%/8);
    top: 50%;
    transform: translateY(-50%);
    @media screen and (max-width: $md-max) {
      box-sizing: border-box;
      padding-right: 0px;
      margin-bottom: 72px;
    }
    @media screen and (max-width: $sm-max) {
      margin-bottom: 0px;
    }
  }

  .slide-outer {
    display: flex;
  }
  
  .slide-content {
    position: relative;
    margin: auto;
    width: 100%;
    padding-top: calc(100% * 1/8);
    padding-bottom: calc(100% * 1/8);
    max-width: calc((100% * 6/8) - 48px); // 48px to account for margin
  }

  .full-span-outer {
    display: flex;
  }

  .full-span-inner {
    position: relative;
    margin: auto;
    padding-top: 80px;
    padding-bottom: 80px;
    max-width: 100% * 5/8;
    @media screen and (max-width: $sm-max) {
      max-width: calc(100% - 24px - 24px - 12px); // additional 12 overall added to make less cramped
    }
  }
</style>

<style lang="scss">
  @import '../styles/variables';
</style>


